import { ALREADY_LOGIN, FETCH_ADS, Fetch_SINGLE_AD ,GOOGLE_LOGIN,LOGIN,LOGOUT} from "../Types/Types"
import { db,auth } from "../../Config/Firebase"
import {Firebase} from "../../Config/Firebase"
// import { dispatch } from "react-hot-toast/dist/core/store";
// import { GoogleAuthProvider } from "firebase/auth"

export const doLogin=(email,password)=>async(dispatch)=>{
try {
    const userCredential=await auth.signInWithEmailAndPassword(email,password);
    var user=userCredential.user;
    console.log("user",user.uid);
    dispatch({
        type:LOGIN,
        payload:user,
    })
} catch (error) {
    alert(JSON.stringify(error))
    console.log(error);
}
}
export const alreadyLoginUser=(setPending)=>async(dispatch)=>{
    try {
        const LoginUser= await Firebase.auth().onAuthStateChanged((user)=>{
            if(user){
                var uid = user.uid;
                console.log("uid",uid);
                dispatch({
                    payload:user,
                type:ALREADY_LOGIN
                
                })
                setPending(false)
            }
            else{
                setPending(false)
            }
            console.log("LoginUser",LoginUser);
        })
    } catch (error) {
        alert(JSON.stringify(error))
    }
}
export const doSignUp=(user)=>async(dispatch)=>{
try {
    //FIREBASE LOGIN
    const userCredential=await auth.createUserWithEmailAndPassword(user.email,user.password);
    var userData= userCredential.user;
    // FIREBASE DATABASE
    await db.collection("users").add({
      ...user,
      uid:userData.uid  
    })
    dispatch({
        type:LOGIN,
        payload:userData,
    })
} catch (error) {
    alert(JSON.stringify(error))
    console.log(error);
}
}

export const doLogout=()=>async(dispatch)=>{
try {
    const res = await auth.signOut();
    console.log("user",res);
    dispatch({
        type:LOGOUT,
    })
} catch (error) {
    alert(JSON.stringify(error))
console.log(error);
}
}

export const LoginWithGoogle=(setLoading)=>async(dispatch,result)=>{
try {
    setLoading(true)
    var provider = new Firebase.auth.GoogleAuthProvider();
    await provider.addScope('https://www.googleapis.com/auth/contacts.readonly');
    console.log("provider",provider);
    Firebase.auth()
    .signInWithPopup(provider)
    var user = result.user;
   console.log(user);
    dispatch({
        type:GOOGLE_LOGIN,
    })
} catch (error) {
    alert(JSON.stringify(error))
}
finally{
    setLoading(false)
}
}
export const LoginWithFacebook=()=>async(dispatch)=>{
try {
    var FacebookLogin = new Firebase.auth.FacebookAuthProvider();
    console.log("FacebookLogin",FacebookLogin);
    Firebase
  .auth()
  .signInWithPopup(FacebookLogin)
    dispatch({
        type:GOOGLE_LOGIN,
    })
} catch (error) {
    
}
}


export const FetchAdsAction = (setLoading) => async (dispatch) => {
    try {
        setLoading(true)
        const Snapshot = await db.collection("CardsAds").get()
        let CardsAds = []
        Snapshot.forEach((doc) => {
            CardsAds.push({
                DocId: doc.id,
                ...doc.data()
            })
        })
        console.log("data from firebase into actions", CardsAds);
        dispatch({
            payload: CardsAds,
            type: FETCH_ADS

        });
    } catch (error) {
        console.log(error);
        alert(error)
    }
    finally {
        setLoading(false)
    }
}
