import { ALREADY_LOGIN, FACEBOOK_LOGIN, GOOGLE_LOGIN, LOGIN, LOGOUT } from "../Types/Types"
const initialState = {
    isUserLoggedIn: false,
    user: null
}
export default function AuthReducer(state = initialState, action) {
    switch (action.type) {
        case LOGIN:
            return {
                ...state,
                isUserLoggedIn: true,
                user: action.payload
            }
        case LOGOUT:
            return {
                ...state,
                isUserLoggedIn: false,
                user: null
            }
            case GOOGLE_LOGIN:
                return{
                    ...state,
                isUserLoggedIn:true,
                user:action.payload
                }
            case FACEBOOK_LOGIN:
                return{
                    ...state,
                    isUserLoggedIn:true,
                    user:action.payload
                }

                case ALREADY_LOGIN:
                    return{
                        ...state,
                        isUserLoggedIn:true,
                        user:action.payload
                    }


        default:
            return state;
    }
}