export default function AddItemFields() {
  const [Name, Email, Title, Price, Type, City, State, PhoneNumber, Address, Description, Image, setName, setType, setEmail, setPrice, setState, setCity, setAddress, setDescription, setImage, setPhoneNumber, setTitle] = Fetch()

  return (
    <Container>
      <Form>
        <Form.Row>
          <Form.Group as={Col} controlId="Name">
            <Form.Label>Name</Form.Label>
            <Form.Control type="text" placeholder="Name" value={Name} onChange={(e) => setName(e.target.value)} />
          </Form.Group>
          <p>{Name}</p>

          <Form.Group as={Col} controlId="Enter Email">
            <Form.Label>Email</Form.Label>
            <Form.Control type="Email" placeholder="Enter Email" value={Email} onChange={(e) => setEmail(e.target.value)} />
          </Form.Group>
          <p>{Email}</p>
          <Form.Group as={Col} controlId="Enter Title">
            <Form.Label>Title</Form.Label>
            <Form.Control type="text" placeholder="Enter Title" value={Title} onChange={(e) => setTitle(e.target.value)} />
          </Form.Group>
          <p>{Title}</p>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col} controlId="Price">
            <Form.Label>Price</Form.Label>
            <Form.Control type="text" placeholder="Price" value={Price} onChange={(e) => setPrice(e.target.value)} />
          </Form.Group>
          <p>{Price}</p>

          <Form.Group as={Col} controlId="Type/Category">
            <Form.Label>Type/Category</Form.Label>
            <Form.Control type="text" placeholder="Type/Category" value={Type} onChange={(e) => setType(e.target.value)} />
          </Form.Group>
          <p>{Type}</p>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col} controlId="City">
            <Form.Label>City</Form.Label>
            <Form.Control placeholder="City" value={City} onChange={(e) => setCity(e.target.value)} />
          </Form.Group>
          <p>{City}</p>

          <Form.Group as={Col} controlId="formGridState">
            <Form.Label>State</Form.Label>
            <Form.Control as="select" defaultValue="Choose...">
              <option value={State} onChange={(e) => setState(e.target.value)}>Choose...</option>
              {
                ListOfCities.map((cities) => {
                  return (
                    <option >{cities}</option>
                  )
                })
              }

            </Form.Control>
            <p>{State}</p>
          </Form.Group>


          <Form.Group as={Col} controlId="Phone Number">
            <Form.Label>Phone Number</Form.Label>
            <Form.Control placeholder="Phone Number" value={PhoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} />
          </Form.Group>
          <p>{PhoneNumber}</p>
        </Form.Row>
        <Form.Group as={Col} controlId="Address">
          <Form.Label>Address</Form.Label>
          <Form.Control placeholder="Address" value={Address} onChange={(e) => setAddress(e.target.value)} />
        </Form.Group>
        <p>{Address}</p>
        <Form.Group as={Col} controlId="Description">
          <Form.Label>Description</Form.Label>
          <Form.Control placeholder="Description" value={Description} onChange={(e) => setDescription(e.target.value)} />
        </Form.Group>
        <p>{Description}</p>
        <Form.Group as={Col}>
          {/* <UploadButton value={Image}  onChange={(e) => setImage(e.target.value)}/>
        <img src={Image} alt="" /> */}

          <form action="/action_page.php">
            <input type="file" id="myFile" name="filename" value={Image} onChange={(e) => setImage(e.target.value)} />
          </form>
        </Form.Group>



        <AcceptButton />
      </Form>
    </Container>
  );
}

